.input{position:relative;display:inline-block;vertical-align:middle}.input.input--disabled{cursor:not-allowed}.input .input__original{outline:none;padding:5px 8px}.input__placeholder{left:4px;padding:0 2px;position:absolute;top:10px;-webkit-transition:all .2s ease-out;transition:all .2s ease-out;font-size:12px}.input__dropdown{left:0;list-style-type:none;margin-top:5px;min-width:140px;padding:0;position:absolute;white-space:nowrap;z-index:10;border:1px solid #ccc}.input__dropdown:empty{display:none}.input__option{background-color:#fff}.input__option:hover{background-color:#ccc}.input__option.input__option--active{color:red}.fadeInUp{-webkit-transition:all .2s ease-out;transition:all .2s ease-out}.input__placeholder--enter{top:-4px}



